import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Stopwatch from './views/Stopwatch.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/stopwatch',
      name: 'stopwatch',
      component: Stopwatch
    }
  ]
})



